
import { defineComponent, ref } from 'vue'
import { useStore } from 'vuex'
import useFetching, { FetchStatus, ResponseFetching } from '@/hooks/useFetching'
import { IZoneForm } from '@/types'
import PageCard from '@/components/PageCard.vue'
import ZoneForm from '@/views/system/localization/zone/forms/ZoneForm.vue'

export interface IData {
  model: IZoneForm | null;
}

export default defineComponent({
  setup () {
    const store = useStore()

    const [saveForm, saveStatus, saveError] = useFetching((dataForm: IZoneForm) => {
      return store.dispatch('zone/createForm', dataForm)
    }) as ResponseFetching<Promise<IZoneForm>>

    const form = ref()

    return {
      saveForm,
      saveStatus,
      saveError,
      form
    }
  },

  components: {
    PageCard,
    ZoneForm
  },

  computed: {
    isLoading (): boolean {
      return this.saveStatus === FetchStatus.pending
    }
  },

  data (): IData {
    return {
      model: {
        name: 'New zone',
        code: '',
        countryId: null,
        isActive: false
      }
    }
  },

  methods: {
    handleBack () {
      this.$router.push({ name: 'zone' })
    },

    async handleSubmit () {
      if (this.form && await this.form.validate()) {
        const data: IZoneForm = await this.saveForm(this.model)
        if (typeof data !== 'undefined') {
          this.model = data
        }

        if (this.model?.id) {
          this.$router.push({ name: 'zone-update', params: { id: this.model.id } })
        }
      }
    }
  }
})
